.catalog {
  padding-top: 0;

  &__item {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
    position: relative;
    min-height: 350px;

  }

  &__link {
    position: absolute;
    width: 80%;
    height: 100%;
  }

  &__name {
    margin: 0;
    font-size: 18px;
    font-weight: 400;
    text-align: center;
    position: relative;
    display: inline-block;

    &::after {
      content: "";
      width: 0;
      height: 4px;
      background-color: orange;
      display: block;
      position: absolute;
      left: 0;
      bottom: -5px;
      transition: all .3s ease-in-out;
    }
  }

  &__content {
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(3, 1fr);
  }

  &__imagebox {
    width: 100%;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.catalog__item:hover .catalog__name::after {
  width: 100%;
}
