.feedback {
  &__inner {
    display: flex;
    gap: 33px;
    justify-content: space-between;
    align-items: center;
  }

  &__content {
    max-width: 718px;
  }

  &__title {
    color: orange;
    margin-bottom: 15px;
  }

  &__subtitle {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #40444e;
    margin-bottom: 29px;
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 35px;
  }

  &__row {
    display: flex;
    gap: 33px;
  }

  &__label {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;

    & span {
      font-weight: 700;
      font-size: 18px;
      line-height: 21px;
      color: #40444e;
    }
  }

  &__input {
    min-width: 85px;
    background: #F1F1F1;
    border-radius: 35px;
    color: #B6B6B6;
    padding: 10px 20px;

    outline: none;
    border: 1px solid #f9f9f9;

    &::placeholder {
      font-size: 18px;
    }
  }

  &__time {
    display: flex;
  }

  &__question-box {
    width: 100%;
  }

  &__question {
    width: 100%;
  }

  &__btn {
    position: relative;
    height: 40px;
    padding: 10px 45px 10px 20px;
  }

  &__btn::after {
    content: "";
    display: block;
    position: absolute;
    background-color: white;
    transform: rotate(60deg);
    top: -5px;
    right: -40px;
    z-index: 60;
    width: 60px;
    height: 40px;
  }
}

.checkbox__control {
  position: relative;
  width: 12px;
  height: 12px;
  margin-top: 2px;
  background-color: inherit;
  border: 2px solid #dedede;
  float: left;
  cursor: pointer;
}

.checkbox__input {
  position: absolute;
  opacity: 0;
}

.checkbox__input:checked + .checkbox__control:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 8px;
  height: 8px;
  background-color: #f28e00;
}

.checkbox-label {
  display: flex;
  align-items: flex-start;

  & p {
    margin: 0;
    margin-left: 5px;
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
  }
}
