.footer {
  padding: 78px 0 82px;
  background-color: #40444e;
  color: #fff;

  &__inner {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 33px;
  }

  &__image {
    padding-top: 100px;
  }

  &__link {
    color: #fff;
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
    width: max-content;
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: 15px;

    &::after {
      content: '';
      background-color: rgba(255, 255, 255, 0.5);;
      width: 157px;
      height: 1px;
      display: block;
      margin-bottom: 15px;
    }
  }

  &__non-line {
    &::after {
      display: none;
    }
  }

  &__copy {
    margin: 0;
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
    margin-top: 20px;
  }

  &__title {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 300;
    font-size: 18px;
  }

  &__row {
    & p {
      margin: 0;
      font-weight: 300;
      font-size: 18px;
      line-height: 165%;
    }
  }

  &__contacts {
    font-weight: 700;
    & span {
      font-size: 14px;
      line-height: 16px;
    }
  }
}
