.divisions-top {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 33px;
  margin-bottom: 80px;
  align-items: flex-start;
}


.divisions-top {

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
  }

  &__link-box {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-bottom: 5px;
  }

  &__link {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: #40444E;
    color: #fff;
    border-radius: 20px;
    padding: 12px 20px;
    transition: all .3s ease-in-out;
    font-size: 20px;

    @include hover {
      background-color: orange;
    }

    & span {
      font-size: 14px;
    }
  }

  &__name {
    position: relative;
    font-weight: 500;
    font-size: 24px;
    color: #40444E;
    &::before {
        content: '';
        width: 100%;
        height: 2px;
        background-color: #d9d9d9;
        display: block;
        position: absolute;
        top: -7px;
      }

  }

}

.divisions-bottom {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 33px;

  &__element {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__image {
    margin-bottom: 20px;
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 24px;
    margin-bottom: 10px;
    min-height: 75px;
    color: #40444e;
  }

  &__text {
    margin: 0;
    font-weight: 300;
    font-size: 18px;
    color: #40444e;
  }
}
