.info {
  background-image: url(../img/info__back.png);
  background-repeat: no-repeat;
  background-position: center 220px;
  width: 100%;
  height: 600px;
  position: relative;

  &__pagination {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    gap: 8px;
  }
}

.swiper-slide {
  position: relative;
}

.swiper-element {
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
  width: 320px;
  height: 330px;
  position: absolute;
  user-select: none;

  &__image {
    max-width: 300px;
    max-height: 120px;
    margin-bottom: 40px;

    img {
      width: auto;
      max-height: 120px
    }
  }

  &__digit {
    font-weight: 700;
    font-size: 62px;
    line-height: 1;
    letter-spacing: 0;
    color: #40444e;
  }

  &__chapter {
    font-weight: 500;
    font-size: 36px;
    line-height: 0.9;
    color: #40444e;
    margin-bottom: 20px;
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 1;
    z-index: 10;
    transition: 0.25s;
    position: relative;

  }

}



