.header {
  padding-top: 40px;

  &__auth {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 20px;
  }

  &__body {
    display: flex;
    align-items: center;
    gap: 80px;
  }

  &__content {
    width: calc(100% - 183px - 80px);

  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 8px;
    position: relative;
    padding: 7px 0;

    &::after {
      content: '';
      width: 80%;
      height: 1px;
      background-color: gray;
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }

  &__bottom {
    text-align: right;
  }

  &__search {
    position: relative;

    & button {
      position: absolute;
      top: 0;
      left: 10px;
      bottom: 0;
    }

    & input {
      border: none;
      background-color: #d9d9d9;
      width: 240px;
      height: 42px;
      padding: 15px;
      padding-left: 70px;
    }
  }

  &__bottom {
    display: flex;
    justify-content: right;
    align-items: center;
    gap: 33px;
  }

  &__tel {
    color: gray;
  }
}


.auth {
  &__link {
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    color: black;

    &:not(:last-child) {
      margin-right: 33px;
    }
  }
}


.nav {
  &__list {
    display: flex;
  }

  &__item {
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 10px;

    svg {
      transform: rotate(90deg);
      transition: transform .3s ease-in-out;
    }
  }

  &__item:hover svg {
    transform: rotate(270deg);
  }


  &__item:hover &__sub-list  {
    height: auto;
    opacity: 1;
    transition: opacity 0.25s;
    padding-top: 32px;
    padding-bottom: 10px;
  }


  &__item {
    &:not(:last-child) {
      margin-right: 40px;
    }
  }

  &__link {
    font-size: 18px;
    color: black;
    font-weight: 400;
  }

  &__sub-list {
    position: absolute;
    height: 0;
    opacity: 0;
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    top: 40px;
    left: 0;
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 0;
    -ms-flex-direction: row;
    flex-direction: row;
    z-index: 65;
    gap: 35px;
    transition: opacity 0.25s;
  }

  &__sub-item:hover a {
    text-decoration: underline;
  }
}
