.news {
  &__inner {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 33px;
  }

  &__image {
    position: relative;
    min-height: 250px;
    margin-bottom: 30px;

    & img {
      width: auto;
      height: auto;
      max-width: 100%;
      max-height: 300px;
    }
  }

  &__date {
    position: absolute;
    top: 15px;
    left: 15px;
    background-color: #fff;
    color: black;
    border-radius: 20px;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 120px;
  }

  &__name {
    margin: 0;
    margin-bottom: 10px;
    font-size: 18px;
    color: #40444e;
    min-height: 54px;
  }

  &__text {
    margin: 0;
    margin-bottom: 5px;
    font-size: 18px;
    color: #40444e;
  }
}
